/* Module */
.module {
  width: 100%;

  margin: 0;
  padding: 0 15px;
  box-sizing: border-box;

  color: rgba(129, 132, 136, .7);
  font-size: 11px;
  font-weight: 500;
  letter-spacing: 0.3px;
  text-transform: uppercase;
  line-height: 45px;

  border: 1px solid #EDEEEE;
  border-radius: 2px 2px 0 0;
  background-color: #FFFFFF;
}
